<template>
  <div class="index" ref="indexDiv">
    <div class='top_header flex justify_between'>
      <div><!--{{'<'}}--></div>
      <div>我的资质</div>
       <div  > </div>
		<div class="iconSvg"> <icon-svg iconClass="usericon" ></icon-svg>{{userName}}{{workNumber?`(${workNumber})`:''}}</div>

	</div>

      <div class="content">
        <div class="tableTitle">资质证书</div>
        <SearchTable   class="fistTable"   :data="tableData" :tableConfig="tableConfig"
                     @listenToCheckedChange="listenToCheckedChange"     >

        </SearchTable>
        <div class="tableTitle">证书的资质清单</div>

        <SearchTable   class="secondTable"   :data="tableRData" :tableConfig="tableRConfig"   ref="Right">
          <el-table-column slot="option" label="操作" align='center':width="80" >
            <template slot-scope="{ row }" >
                            <span  class="rowSvg">
                              <!--<el-button type="primary" @click="openInfo(row)">查看</el-button>-->
                            </span>
            </template>
          </el-table-column>

        </SearchTable>
      </div>
   </div>
</template>
<script>
  import {getUserInfo } from '@/lib/auth';

   import SearchTable from '@/components/Ftable/index';
  import { rightConfig,leftConfig } from '../tableConfig.js';
  import request from '@/lib/axios.js';
  import {  extend } from 'lodash';
  export default {
    components:{SearchTable},
    data(){
      return {
		  userName:getUserInfo().name,
		  workNumber:getUserInfo().workNumber,
        tableRData:[],
        tableData:[],
        tableRConfig:rightConfig(),
        tableConfig:leftConfig(),
      }
    },
    created(){

      this.getList()
    },
    methods:{
      getList(){

        request({
          url:`${this.$ip}/mms-qualification/qualify/getByUserNumber/${getUserInfo().id}`,
          method: 'get',
        })
          .then((data) => {
            this.tableData =  data.data;
          })
      },

      listenToCheckedChange(row){
        console.log(row);
        this.tableRData=row.qualifyDetailList||[]
      }
    },

  }
</script>
<style lang="less" scoped>
  .index {
    height:calc(100vh - 69px);
 .content{
   padding: 10px 20px 20px 20px ;
 }
    .tableTitle{
      text-align: center;
      font-weight: bold;
      margin: 10px 0;
    }
    .secondTable,.fistTable{
      /deep/ .el-table__body-wrapper{
      height: calc(50vh - 160px) !important;
      overflow-y: auto;
      }
    }
    .top_header {
      line-height: 68px;
      height: 68px;
       font-size: 24px;

      background: #2D4157;
      color: #fff;
      padding: 0 10px;
     }

    .rowSvg{
      .el-button{
        padding:5px 10px;
      }
    }

  }
</style>



